<template>
    <div id="progressReportAdd">
        <div class="showForm">
            <el-form ref="showForm" >
                <table class="form_table" :model="showForm">
                    <thead>
                        <tr><th colspan="4"></th></tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td></td>
                            <td>
                                <el-input v-model="showForm.subjectName"></el-input>
                            </td>
                            <td></td>
                            <td>
                                <el-input v-model="showForm.projectNo"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <el-input v-model="showForm.bearerUnit"></el-input>
                            </td>
                            <td></td>
                            <td>
                                <el-date-picker
                                    v-model="showForm.fillTime"
                                    type="date"
                                    value-format="yyyy-MM-dd"
                                    placeholder="">
                                </el-date-picker>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <el-input v-model="showForm.projectLeader"></el-input>
                            </td>
                            <td></td>
                            <td>
                                <el-input v-model="showForm.projectLeaderPhone"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <el-input v-model="showForm.primaryContacts"></el-input>
                            </td>
                            <td></td>
                            <td>
                                <el-input v-model="showForm.primaryContactsPhone">
                            </el-input>
                            </td>
                        </tr>
                        <tr style="height:50px;">
                            <td></td>
                            <td colspan="3">
                                <el-radio v-model="showForm.progress" label="45"></el-radio>
                                <el-radio v-model="showForm.progress" label="46"></el-radio>
                                <el-radio v-model="showForm.progress" label="47"></el-radio>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4></h4>
                                <p>{{ taskForm.length }}</p>
                                <ol>
                                    <li v-for="(item,index) in taskForm" :key="index">
                                        <span class="index">{{ index + 1 }}.</span>
                                        <el-input 
                                            v-model="taskForm[index]"
                                            :autosize="{ minRows:1 }"
                                            type="textarea"
                                            @input="autoAdd">
                                        </el-input>
                                    </li>
                                </ol>
                            </td>
                        </tr>
                        <!--  -->
                        <tr class="item">
                            <td colspan="4">
                                <h4></h4>
                                <p>
                                    {{ progressForm.dataList.length - 1}}
                                    <el-input class="_inline" v-model="showForm.progressCompletedPercentage"></el-input>
                                    %
                                    </p>
                                <ol>
                                    <li v-for="(item,index) in progressForm.dataList" :key="index">
                                        <span class="index">{{ index + 1 }}.</span>
                                        <el-input 
                                            v-model="progressForm.dataList[index]"
                                            :autosize="{ minRows:1 }"
                                            type="textarea"
                                            @input="autoAdd">
                                        </el-input>
                                    </li>
                                </ol>
                            </td>
                        </tr>
                        <!--  -->
                        <tr class="item">
                            <td colspan="4">
                                <h4></h4>
                                <ol style="line-height:1.8;">
                                    <li>
                                        <el-input class="_inline" v-model="showForm.totalFundsInplace"></el-input>
                                        
                                        <el-input class="_inline" v-model="showForm.projectFundsUsed"></el-input>
                                        
                                        <el-input class="_inline" v-model="showForm.totalFunding"></el-input>
                                        %
                                    </li>
                                    <li>
                                        <el-input class="_inline" v-model="showForm.provincialEnvironmentalFundsUsed"></el-input>
                                        
                                        <el-input class="_inline" v-model="showForm.provincialEnvironmentalFundsPercent"></el-input>
                                        %
                                    </li>
                                </ol>
                            </td>
                        </tr>
                        <!--  -->
                        <tr class="item">
                            <td colspan="4">
                                <h4></h4>
                                <ol>
                                    <li v-for="(item,index) in problemForm" :key="index">
                                        <span class="index">{{ index + 1 }}.</span>
                                        <el-input 
                                            v-model="problemForm[index]"
                                            :autosize="{ minRows:1 }"
                                            type="textarea"
                                            @input="autoAdd">
                                        </el-input>
                                    </li>
                                </ol>
                            </td>
                        </tr>
                        <!--  -->
                        <tr class="item">
                            <td colspan="4">
                                <h4></h4>
                                <ol>
                                    <li v-for="(item,index) in planForm" :key="index">
                                        <span class="index">{{ index + 1 }}.</span>
                                        <el-input 
                                            v-model="planForm[index]"
                                            :autosize="{ minRows:1 }"
                                            type="textarea"
                                            @input="autoAdd">
                                        </el-input>
                                    </li>
                                </ol>
                            </td>
                        </tr>
                        <!--  -->
                        <tr class="item">
                            <td colspan="4">
                                <h4></h4>
                                <ol style="line-height:1.8;">
                                    <li>
                                        1. 
                                        <el-date-picker
                                            class="_inline_date"
                                            v-model="showForm.contractAgreedClosingTime"
                                            type="date"
                                            value-format="yyyy-MM-dd"
                                            placeholder="">
                                        </el-date-picker>
                                    </li>
                                    <li>
                                        2. 
                                        <el-radio v-model="showForm.isComplateContract" label="0" style="line-height:28px;"></el-radio>
                                        <el-radio v-model="showForm.isComplateContract" label="1" style="line-height:28px;"></el-radio>
                                    </li>
                                    <li>
                                        3. 
                                        <el-date-picker
                                            class="_inline_date"
                                            v-model="showForm.estimatedAcceptanceTime"
                                            type="date"
                                            value-format="yyyy-MM-dd"
                                            placeholder="">
                                        </el-date-picker>
                                    </li>
                                </ol>
                            </td>
                        </tr>
                        <!--  -->
                        <tr class="item">
                            <td colspan="4">
                                <h4></h4>
                                <el-input 
                                    v-model="showForm.unitAuditComments"
                                    :autosize="{ minRows:10 }"
                                    type="textarea">
                                </el-input>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </el-form>
            <div class="btn_group">
                <el-button @click="handleSubmit"></el-button>
                <el-button @click="handleBack"></el-button>
            </div>
            
        </div>
    </div>
</template>

<script>
    export default {
        name:'progressReportAdd',
        data(){
            return {
                radio: '',
                showForm: {
                    // 
                    subjectName: '17',
                    projectNo: '18',
                    bearerUnit: '1',
                    fillTime: '',
                    projectLeader: '13',
                    projectLeaderPhone: '14',
                    primaryContacts: '8',
                    primaryContactsPhone: '9',
                    progress: '45',
                    // 
                    progressCompletedPercentage: '28',
                    // 
                    totalFundsInplace: '21',
                    projectFundsUsed: '12',
                    totalFunding: '20',
                    provincialEnvironmentalFundsUsed: '16',
                    provincialEnvironmentalFundsPercent: '15',
                    // 
                    contractAgreedClosingTime: '',
                    isComplateContract: '0',
                    estimatedAcceptanceTime: '',
                    // 
                    unitAuditComments: '22'
                },
                taskForm: [''],
                progressForm: {
                    progress: '60',
                    dataList: ['']
                },
                fundsForm: {
                    num: 50
                },
                problemForm: [''],
                planForm: [''],
                timeForm: {
                    date: "2019824",
                    radio: ''
                },
                paramsData: {
                    id: this.$route.params.id
                }
            }
        },
        methods:{
            handleSubmit() {
                this.axios({
                    url: 'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/environment/progress/insertProgress',
                    method: 'post',
                    data: this.showForm
                }).then((res) => {
                    console.log(res);
                    let id = res.data.data;
                    // 
                })


                // const loading = this.$loading({
                //     lock: true,
                //     text: '...',
                //     spinner: 'el-icon-loading',
                //     background: 'rgba(255,255,255,0.7)'
                // });
                // setTimeout(() => {
                //     loading.close();
                //     this.$alert('', '', {
                //         confirmButtonText: '',
                //         type: 'success',
                //         callback: action => {
                //             this.$router.go(-1);
                //         }
                //     });
                // },2000);
            },
            handleBack() {
                this.$router.go(-1);
            },
            autoAdd() {
                let taskFormFlag = true;
                for(let i in this.taskForm) {
                    if(this.taskForm[i].match(/^[ ]*$/)) {
                        taskFormFlag = false;
                    }
                }
                if(taskFormFlag) {
                    this.taskForm.push("");
                }
                let progressFormFlag = true;
                for(let i in this.progressForm.dataList) {
                    if(this.progressForm.dataList[i].match(/^[ ]*$/)) {
                        progressFormFlag = false;
                    }
                }
                if(progressFormFlag) {
                    this.progressForm.dataList.push("");
                }
                let planFormFlag = true;
                for(let i in this.planForm) {
                    if(this.planForm[i].match(/^[ ]*$/)) {
                        planFormFlag = false;
                    }
                }
                if(planFormFlag) {
                    this.planForm.push("");
                }
            }
        }
    }
</script>

<style lang="less">
    #progressReportAdd{
        .showForm{
            table.form_table{
                min-width: 1000px;
                width: 65.4%;
                @media screen and ( max-width: 1600px ) {
                    width: 87.4%;
                }
                .el-radio {
                    margin: 0 50px;
                }
                .item {
                    td {
                        * {
                            text-align: left;  
                        }
                        h4 {
                            padding: 15px 0;
                        }
                        p {
                            margin-bottom: 5px;
                        }
                        background-color: #fff !important;
                        padding: 0 10px;
                        ol {
                            padding-bottom: 5px;
                            list-style-type: none;
                            li {
                                display: flex;
                                // line-height: 1.8;
                                .index {
                                    display: inline-block;
                                    padding-top: 2px;
                                    height: 16px;
                                }
                                .el-textarea {
                                    display: inline-block;
                                    .el-textarea__inner {
                                        // margin-top: 10px;
                                        padding-left: 10px;
                                    }
                                    
                                }
                            }
                        }
                    }
                    .el-textarea {
                        margin-bottom: 10px;
                    }
                }
                .el-date-editor {
                    width: 100%;
                    .el-input__inner {
                        width: 100%;
                    }
                }
                ._inline {
                    display: inline-block;
                    width: 40px;
                    height: 18px;
                    .el-input__inner{
                        height: 100%;
                        padding: 0 3px;
                        border-bottom: 1px solid #000;
                        border-radius: 0;
                    }
                    
                }
                ._inline_date{
                    width: 300px;
                    height: 28px;
                    // line-height: 30px;
                    .el-input__icon {
                        display: none;
                    }
                    .el-input__inner {
                        height: 100%;
                    }
                }
            }
            .qianding{
                padding: 10px 0;
                span{
                    line-height: 30px;
                }
                .el-input{
                    width: auto;
                    .el-input__inner{
                        line-height: 30px;
                        height: 30px;
                    }
                }
            }
        }
        .btn_group {
            margin-top: 10px;
            padding-bottom: 30px;
        }
    }
</style>



