// 
let validatePhone = (val) => {
    var reg = /^1[3456789]\d{9}$/;
    if(!reg.test(val)) {
        return true;
    }
    return false;
};

// 
let validatePwd = (val) => {
    if(val.length < 6 || val.length > 18) {
        return true;
    }
    return false;
}

// 
let validateCard = (val) => {
    let area = { 
        11: "", 
        12: "", 
        13: "", 
        14: "", 
        15: "", 
        21: "", 
        22: "", 
        23: "", 
        31: "", 
        32: "", 
        33: "", 
        34: "", 
        35: "", 
        36: "", 
        37: "", 
        41: "", 
        42: "", 
        43: "", 
        44: "", 
        45: "", 
        46: "", 
        50: "", 
        51: "", 
        52: "", 
        53: "", 
        54: "", 
        61: "", 
        62: "", 
        63: "", 
        64: "", 
        65: "", 
        71: "", 
        81: "", 
        82: "", 
        91: ""
    } 
    let retflag = false; 
    let Y, JYM; 
    let S, M; 
    let idcard_array = new Array(); 
    idcard_array = val.split(""); 
    let ereg;
    // 
    if(area[parseInt(val.substr(0, 2))] == null) 
        return true; 
    // 
    switch(val.length) { 
        case 15: 
            if((parseInt(val.substr(6, 2)) + 1900) % 4 == 0 || ((parseInt(val.substr(6, 2)) + 1900) % 100 == 0 && (parseInt(val.substr(6, 2)) + 1900) % 4 == 0)) { 
                ereg = /^[1-9][0-9]{5}19[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}$/; // 
            }else { 
                ereg = /^[1-9][0-9]{5}19[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|1[0-9]|2[0-8]))[0-9]{3}$/; // 
            } 
            if(ereg.test(val)) { 
                return false; 
            }else { 
                return true;
            }
            break; 
        case 18: 
            if(parseInt(val.substr(6, 4)) % 4 == 0 || (parseInt(val.substr(6, 4)) % 100 == 0 && parseInt(val.substr(6, 4)) % 4 == 0)) { 
                ereg = /^[1-9][0-9]{5}(19|20)[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}[0-9Xx]$/; // 
            }else { 
                ereg = /^[1-9][0-9]{5}(19|20)[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|1[0-9]|2[0-8]))[0-9]{3}[0-9Xx]$/; // 
            } 
            if (ereg.test(val)) { // 
            // 
                S = (parseInt(idcard_array[0]) + parseInt(idcard_array[10])) * 7 + (parseInt(idcard_array[1]) + parseInt(idcard_array[11])) * 9 + (parseInt(idcard_array[2]) + parseInt(idcard_array[12])) * 10 + (parseInt(idcard_array[3]) + parseInt(idcard_array[13])) * 5 + (parseInt(idcard_array[4]) + parseInt(idcard_array[14])) * 8 + (parseInt(idcard_array[5]) + parseInt(idcard_array[15])) * 4 + (parseInt(idcard_array[6]) + parseInt(idcard_array[16])) * 2 + parseInt(idcard_array[7]) * 1 + parseInt(idcard_array[8]) * 6 + parseInt(idcard_array[9]) * 3; 
                Y = S % 11; 
                M = "F"; 
                JYM = "10X98765432"; 
                M = JYM.substr(Y, 1); // 
                if (M == idcard_array[17]) 
                    return false; //ID 
                else 
                    return true; 
            }else 
                return true; 
            break; 
        default: 
            return true; 
            break; 
    } 
}

// 
let validateEmail = (val) => {
    var reg = /^\w+((.\w+)|(-\w+))@[A-Za-z0-9]+((.|-)[A-Za-z0-9]+).[A-Za-z0-9]+$/; //
    if(!reg.test(val)){ //
        return true;
    }else {
        return false;
    }
}

export default {
    validatePhone,
    validatePwd,
    validateCard,
    validateEmail
}


