<template>
    <div id="subjectApplyShow">
         <div class="showForm">
            <el-form ref="showForm" :model="showForm">
                <table class="form_table">
                    <thead>
                        <tr><th colspan="4"></th></tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td></td>
                            <td>
                                <el-input disabled v-model="showForm.topicName" ></el-input>
                            </td>
                            <td></td>
                            <td>
                                <el-input disabled v-model="showForm.topicNumber"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td colspan="3">
                                <el-input disabled v-model="showForm.subjectUndertakingUnit" ></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td colspan="3">
                                <el-radio-group v-model="showForm.unitNature" >
                                    <span v-for="(item,index) in unitNatureOptions" :key = index>
                                        <el-radio disabled :label="item.id">{{item.content}}</el-radio>
                                    </span>
                                </el-radio-group>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <el-input disabled v-model="showForm.projectLeader" ></el-input>
                            </td>
                            <td></td>
                            <td>
                                <el-input disabled v-model="showForm.projectLeaderPhone" ></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <el-input disabled v-model="showForm.projectLeaderMail" ></el-input>
                            </td>
                            <td></td>
                            <td>
                                <el-input disabled v-model="showForm.postalAddress" ></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <el-input disabled v-model="showForm.agreementStartTime" ></el-input>
                            </td>
                            <td></td>
                            <td>
                                <el-input disabled v-model="showForm.agreementEndTime" ></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <!-- <el-input v-model="showForm.applicationAcceptanceTime" ></el-input> -->
                                <el-date-picker
                                    v-model="showForm.applicationAcceptanceTime"
                                    type="date"
                                    disabled
                                    value-format="yyyy-MM-dd"
                                    placeholder="">
                                </el-date-picker>
                            </td>
                            <td></td>
                            <td>
                                <el-radio-group v-model="showForm.applicationAcceptanceMode" >
                                    <span v-for="(item,index) in applicationAcceptanceModeOptions" :key = index>
                                        <el-radio disabled :label="item.id">{{item.content}}</el-radio>
                                    </span>
                                </el-radio-group>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td colspan="3">
                                <el-input disabled v-model="showForm.applicationAcceptancePlace" ></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <el-input disabled v-model="showForm.acceptanceContact" ></el-input>
                            </td>
                            <td></td>
                            <td>
                                <el-input disabled v-model="showForm.acceptanceContactPhone" ></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td colspan="3">
                                <el-input 
                                  disabled
                                  type="textarea"
                                  v-model="showForm.mainContentSituation"
                                  >
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td colspan="3">
                                <el-input 
                                 disabled
                                 type="textarea"
                                 v-model="showForm.submissionAchievementsSituation"
                                 >
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td colspan="3"> 
                                <el-checkbox-group v-model="showForm.submitInventory">
                                    <span v-for="(item,index) in submitInventoryOptions" :key="index">
                                        <el-checkbox disabled :label="item.id">{{item.content}}</el-checkbox>
                                    </span>
                                </el-checkbox-group>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td colspan="3">
                                <el-input 
                                 type="textarea"
                                 disabled
                                 v-model="showForm.subjectUndertakingUnitOpinion"
                                 ></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td colspan="3">
                                <el-input 
                                 disabled
                                 type="textarea"
                                 v-model="showForm.environmentalDepartmentsOpinion"
                                 ></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td colspan="3">
                                <el-input 
                                 disabled
                                 type="textarea"
                                 v-model="showForm.provinceAssessmentCenterOpinion"
                                 ></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td colspan="3">
                                <el-input 
                                 disabled
                                 type="textarea"
                                 v-model="showForm.competentDepartmentOinion"
                                 ></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td colspan="3" style="height:50px;"> 
                                <el-input disabled v-model="showForm.submitInventoryUrl"></el-input>
                                <!-- <input type="file" id="submitInventoryFile"> -->
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td colspan="3" style="height:50px;">
                                <el-input 
                                 disabled
                                 v-model="showForm.applicationAcceptanceUrl"
                                 ></el-input>
                                 <!-- <input type="file" id="applicationAcceptanceFile"> -->
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td colspan="3" style="height:50px;">
                                <el-input disabled v-model="showForm.achievementsUrl" ></el-input>
                                <!-- <a class='download' :href='showForm.achievementsUrl' download=""  title="">{{}}</a> -->
                                <!-- <input type="file" id="achievementsFile"> -->
                            </td>
                        </tr>
                    </tbody>
                 </table>

                <!--  -->
                <table v-show="showForm.extranetCheckApplyStateList.length>0" class="form_table1">
                    <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr v-for="(item,index) in showForm.extranetCheckApplyStateList">
                        <td>
                            <el-input disabled v-model="item.fistHandler"></el-input>
                        </td>
                        <td>
                            <el-input disabled v-model="item.secondHandler"></el-input>
                        </td>
                        <td>
                            <el-input disabled v-model="item.auditStep"></el-input>
                        </td>
                        <td style="width:16%;">
                            <el-input disabled v-model="item.firstHandleTime"></el-input>
                        </td>
                        <td>
                            <el-input disabled v-model="item.state"></el-input>
                        </td>
                        <td>
                            <el-input disabled v-model="item.handleContent"></el-input>
                        </td>
                        <td style="width:16%;">
                            <el-input disabled v-model="item.secondHandleTime"></el-input>
                        </td>
                    </tr>
                </table>
            </el-form>
            <el-button type="primary" v-show="this.$route.params.isShow == 2"  @click="handleExime"></el-button>
            <el-button type="primary" v-show="this.$route.params.isShow == 2"  @click="handleReject"></el-button>
            <el-button type="primary"  @click="handleBack"></el-button>
        </div>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose">
            <el-form ref="form" :model="form" :rules="rules" label-width="80px">
                <el-form-item label="" prop="reason">
                    <el-input 
                     type="textarea" 
                     v-model="form.reason"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleBtnConfirm('form')"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
export default {
    name:'subjectApplyShow',
    data(){
        return{
            showForm:{
                topicName:'',
                topicNumber:'',
                subjectUndertakingUnit:'',
                unitNature:'',
                projectLeader:'',
                projectLeaderPhone:'',
                projectLeaderMail:'',
                postalAddress:'',
                agreementStartTime:'',
                agreementEndTime:'',
                applicationAcceptanceTime:'',
                applicationAcceptanceMode:'',
                applicationAcceptancePlace:'',
                acceptanceContact:'',
                acceptanceContactPhone:'',
                mainContentSituation:'',
                submissionAchievementsSituation:'',
                submitInventory:[],
                subjectUndertakingUnitOpinion:'',
                environmentalDepartmentsOpinion:'',
                provinceAssessmentCenterOpinion:'',
                competentDepartmentOinion:'',
                // applicationAcceptanceUrl:'',
                // achievementsUrl:''
            },
            unitNatureOptions:[{//
                "id": 1,
                "classification": null,
                "content": "",
                "classificationId": null,
                "contentId": null,
                "state": null
            }],
            applicationAcceptanceModeOptions:[{//
                id:'',
                content:''
            }],
            submitInventoryOptions:[{//
                id:'',
                content:''
            }],
            paramsData:{
                id:this.$route.params.id,
                arrays:this.$route.params.arrays,
                isShow:this.$route.params.isShow
            },
            form:{
                reason:''
            },
            rules: {//
                reason:[
                    { required: true, message: '', trigger: 'blur' }
                ],
            },
            dialogVisible:false
        }
    },
    methods:{
        // 
        handleExime(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/apply/examine',
                params:{
                    id:_this.paramsData.id[0],
                    type:true
                }
            }).then(function(res){
                if(res.data.resultFlag == 1){
                    _this.$alert('', '', {
                        confirmButtonText: '',
             type: 'warning'
           }).then(() => {
                        _this.$router.push('/');
                    }).catch(() => {
                        _this.$router.push('/');
                    });
                }else{
                    _this.$alert('', '', {
                        confirmButtonText: '',
             type: 'warning'
           }).then(() => {
                        _this.$router.back(0)
                    }).catch(() => {
                        _this.$router.back(0)
                    });
                }
            }).catch(function(err){
                console.log(err)
            })
        },
        // 
        handleReject(){
            this.dialogVisible = true
        },
        //
        handleClose(){
            this.dialogVisible = false
        },
        // 
        handleBtnConfirm(formReason){
            let _this = this
            _this.$refs[formReason].validate((valid) => {
                if (valid) {
                   this.axios({
                        method:'POST',
                        url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/apply/examine',
                        params:{
                            type:false,
                            reason:_this.form.reason,
                            id:_this.paramsData.id[0]
                        }
                    }).then(function(res){
                        _this.$alert('', '', {
                            confirmButtonText: '',
                            type:'warning',
                        }).then(() => {
                            _this.$router.back(0)
                        }).catch(() => {
                            _this.$router.back(0)
                        })
                    }).catch(function(err){
                        console.log(err)
                    })
                } else {
                    return false;
                }
            });
        },
        // 
        handleBack(){
            this.$router.back(-1)
        },
        // 
        getUnitNature(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/unitNature',
            }).then(function(res){
                _this.unitNatureOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        //
        getApplicationAcceptanceModeOptions(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/applicationAcceptance',
            }).then(function(res){
                _this.applicationAcceptanceModeOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        // 
        getSubmitInventoryOptions(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/applicationSubmitList'
            }).then(function(res){
                _this.submitInventoryOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        // id
        getInfoById(id){
            if(this.paramsData.arrays != '' || this.paramsData.arrays != undefined){
                this.paramsData.arrays.map((item) => {
                    if(id == item.id){
                        this.showForm = item
                        this.showForm.submitInventory = JSON.parse(item.submitInventory)
                        return;
                    }
                })
            }
        }
        
    },
    mounted(){
        this.getUnitNature()//
        this.getApplicationAcceptanceModeOptions()//
        this.getSubmitInventoryOptions()//
        this.getInfoById(this.paramsData.id)
    }
}
</script>
<style lang="less">
#subjectApplyShow{
    .showForm{
        .el-form{
            .el-table{
                min-width: 900px;
            }
            .form_table1{
                margin-top: 40px;
                width: 70%;
                min-width: 100px;
                tr{
                    td{
                        border-left: 1px solid #e0e0e0;
                        border-bottom: 1px solid #e0e0e0;
                        height: 50px;
                        text-align: center;
                        &:last-child{
                            border-right: 1px solid #e0e0e0;
                        }
                    }
                    &:first-child{
                        border-top: 1px solid #e0e0e0;
                        td{
                            background-color:#e5f3ff;
                        }
                    }
                }
            }
            td{
                text-align: left;
            }
            .el-select{
                width: 100%;
            }
            .el-radio{
                line-height: 50px;
                margin: 0 10px;
                .el-radio__input{
                    padding-right: 5px;
                }
                .el-radio__input.is-+span.el-radio__label{
                    color: #000;
                }
                .el-radio__input.is-checked .el-radio__inner{
                    background-color: #0abd90;
                    border-color: #0abd90;
                }
                .el-radio__input.is-.is-checked .el-radio__inner::after{
                    background-color: #fff;
                }
            }
            textarea{
                min-height: 100px !important;
                padding: 0 10px;
            }
            .el-checkbox-group{
                >span{
                    margin:5px 10px;
                    display: inline-block;
                    .el-checkbox{
                        min-width: 120px;
                        
                        .el-checkbox__label{
                            display: inline-block;
                        }
                    } 
                }
            }
            .el-checkbox__label{
                margin-left: 5px;
            }
            #achievementsFile,#applicationAcceptanceFile,#submitInventoryFile{
                margin-left:10px;
            }
            .el-date-editor{
                .el-input__inner{
                    padding-left: 30px;
                }
                .el-input__prefix{
                    line-height: 50px;
                }
            }
        }
    }
    .el-dialog__wrapper{
        .el-dialog{
            margin-top: 0 !important;
        }
        .el-form{
            .el-form-item__label{
                width: 200px !important;
            }
            .el-form-item__content{
                margin-left: 200px !important;
            }
            textarea{
                min-height: 278px !important;
                padding: 10px;
            }
        }
        .el-dialog__footer{
            text-align: center;
        }
    }
}
</style>


