<template>
    <div id="subjectAuditedList">
         <!--  -->
        <div class="queryForm">
            <el-form ref="queryForm" :model="queryForm" >
                <el-form-item label="" >
                    <el-input v-model="queryForm.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="queryForm.unit"></el-input>
                </el-form-item>
                <el-form-item label="">
                     <el-select v-model="queryForm.unit_nature" placeholder="">
                        <el-option 
                         v-for="item in queryForm.unitOptions"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
                        </el-option>
                     </el-select>
                </el-form-item>
                <el-form-item label="" >
                    <el-input v-model="queryForm.person_liable"></el-input>
                </el-form-item>
            </el-form>
            <el-button type="primary" ></el-button>
        </div>

        <!--  -->
        <div class="showList">
            <!--  -->
            <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange">
                <el-table-column
                type="selection"
                align="center">
                </el-table-column>
                <el-table-column
                type="index"
                label=""
                align="center">
                </el-table-column>
                <el-table-column
                prop="name"
                label=""
                :show-overflow-tooltip="true"
                align="center">
                 <template slot-scope="scope">
                    <router-link :to="{
                        name:'SubjectApplyShow',
                        params:{
                            name:scope.row.id
                        }
                        }"> 
                        {{scope.row.name}}
                    </router-link>
                </template>
                </el-table-column>
                <el-table-column
                prop="unit"
                label=""
                align="center">
                </el-table-column>
                <el-table-column
                prop="unit_nature"
                label=""
                align="center">
                </el-table-column>
                <el-table-column
                prop="content"
                label=""
                align="center">
                </el-table-column>
                <el-table-column
                prop="person_liable"
                label=""
                align="center">
                </el-table-column>
                <el-table-column
                prop="startDate"
                label=""
                align="center">
                </el-table-column>
                <el-table-column
                prop="endDate"
                label=""
                align="center">
                </el-table-column>
                <el-table-column
                prop="isUpload"
                label=""
                align="center">
                </el-table-column>
                <el-table-column
                label=""
                align="center">
                 <template>
                    <el-button></el-button>
                </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <pages
                :total='fenye.total'
                :currentPage='fenye.pageNum'
                :pageSize='fenye.pageSize'
                :pageSizes="fenye.pageSizes"
                @handleCurrentChangeNum="handleCurrentChange"
                @handleSizeChangeNum="handleSizeChange"
            ></pages>
        </div>
    </div>
</template>
<script>
export default {
    name:'subjectAuditedList',
    data(){
        return{
            queryForm:{
                name:'',
                unit:'',
                unitOptions:[{
                    value:'0',
                    label:''
                },{
                    value:'1',
                    label:''
                }],
                unit_nature:'',
                person_liable:''
            },
            tableData:[{
                name:'11',
                unit:'11',
                unit_nature:'11',
                content:'11',
                person_liable:'11',
                startDate:'11',
                endDate:'11',
                isUpload:'11'
            }],
            fenye:{
                total:400, //
                pageNum:1,
                pageSize:100, //
                pageSizes:[100,30,40,50] //
            }
        }
    },
    methods:{
        handleSelectionChange:function(){

        },
        handleExamine(){

        },
        handleCurrentChange:function(val){
            console.log('this is currentPange:' + val)
        },
        handleSizeChange:function(val){
            console.log(':' + val)
        }
    }
}
</script>
<style lang="less">
 #subjectAuditedList{
    .queryForm .el-form .el-form-item .el-form-item__label{
        width: 130px;
    }
    .showList{
        min-height: 640px;
        margin-top: 10px;
        .el-table{
            min-height: 590px;
            padding-bottom: 10px;
            .el-button{
                margin: 0;
            }
        }
    }
    .pages{
        .el-button{
            background-color: transparent;
            border: 0;
            margin: 0;
            span{
                line-height: 1;
            }
        }
    }
}
</style>



